.prod-box{
    width: 95%;
    border-radius: 5px;
    border: .5px solid rgba($color: #000000, $alpha: .4);
    height: 100px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    text-align: center;
    line-height: 5;
    background-color: white;
    margin-top: 5px;
    .wrapper-helper{
        display: flex;
    }
    .image{
        width: 150px;
        height: 100px;
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    .title-box{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }
    .title,
    .sub-title{
        word-wrap: wrap;
    }
    .title{
        max-width: 400px;
        overflow: auto;
        font-size: 1.1rem;
    }
    .sub-title{
        width: 150px;
    }
    .increment-btn{
        cursor: pointer;
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 2;
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}


@media screen and (max-width: 550px){
    .prod-box{
        width: 95%;
        border-radius: 5px;
        border: .5px solid rgba($color: #000000, $alpha: .4);
        height: 100px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        padding: 5px;
        text-align: center;
        line-height: 5;
        background-color: white;
        margin-top: 5px;
        .image{
            width: 70px;
            height: 70px;
            img{
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
        .sub-title{
            width: 130px;
        }
    }
}

@media screen and (max-width: 490px){
    .prod-box{
        height: auto;
        flex-direction: column;
        .wrapper-helper{
            display: flex;
        }
        .title-box{
            height: 50px;
            
        }
    }
}