@mixin inp($wdth: 150px) {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    width: $wdth;
    font-size: .9rem;
    padding: 3px;
    padding-left: 7px;
    border: none;
    border-radius: 3px;
    height: 28px;
    background-color: rgba($color: white, $alpha: .7);
    &:active, &:hover, &:focus {
        outline: 0;
        outline-offset: 0;
    }
}