@mixin btn($bgColor: #819ca9, $wdth: min-content) {
    width: $wdth;
    padding: 8px 15px;
    border-radius: 3px;
    font-size: .9rem;
    border: none;
    background-color: rgba($color: $bgColor, $alpha: .9);
    &:active, &:hover, &:focus {
        outline: 0;
        outline-offset: 0;
    }
    &:active{
        filter: brightness(107%);
    }
    cursor: pointer;
}