.box-viewer{
    text-align: right;
    width: 82%;
}
.viewer{
    padding: 10px;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
}

@media screen and (max-width: 800px){
    .box-viewer{
        width: 100%;
    }
}