.sidebar{
    width: 260px;
    height: 100vh;
    position: sticky;
    top: 0;
    padding: 15px;
    padding-left: 0px;
    display: flex;
    align-items: flex-start;
    box-sizing: border-box;
    flex-direction: column;
    background-color: rgba($color: #29434e, $alpha: .1);
    border-right: .5px solid rgba($color: #000000, $alpha: .3);
    .cat-title{
        margin-top: 0px;
        margin-left: 10px; 
        color: rgba(0,0,0, .9);
    }
    .sidebar__img-box{
        height: 30px;
            margin-left: 10px;

    }
}

@media screen and (max-width: 800px){
    .sidebar{
        padding: 2px;
        padding-top: 10px;
        position: static;
        height: auto;
        width: 100%;
        align-items: center;
        justify-content: center;
        .sidebar__categories{
            display: flex;
            .cat_title{
                display: none;
                
            }
        }
        .sidebar__img-box{
            height: 50px;
            width: 20%;
            padding-top: 10px;
            text-align: center;
        }
    }
}