@import './button.scss';

.card{
    border: .5px solid rgba($color: #29434e, $alpha: .2);
    border-radius: 5px;
    padding: 10px 5px;
    padding-bottom: 20px;
    box-sizing: border-box;
    width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
}

.card-body__title-box{
    padding-top: 10px;
    font-size: 1.1rem;
    text-align: left;
    max-height: 30px;
    max-width: 220px;
    overflow: hidden;
}

.card-body__img-box{
    width: 200px;
    height: 230px;
    .card-body__img{
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.card-body__price-box{
    padding: 10px;
    text-align: right;
    color: rgba($color:#d32f2f, $alpha: 1);
    font-weight: 600;
    font-size: 1.2rem;
}

.card-body__btn-box{
    border-top: 1px solid rgba($color: black, $alpha: .2);
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-top: 15px;
    .btn{
        @include btn;
        &:nth-child(2){
            @include btn(#d32f2f);
        }
    }
}

@media screen and (max-width: 800px){
    .card{
        width: 195px;
        .card-body__img-box{
        width: 180px;
        height: 210px;
            .card-body__img{
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
    }
}

@media screen and (max-width: 427px){
     .card{
        width: 175px;
        padding-bottom: 5px;
        justify-content: space-between;
        .card-body__img-box{
            width: 100%;
            height: 180px;
            .card-body__img{
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
        .card-body__btn-box{
            border-top: 1px solid rgba($color: black, $alpha: .2);
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding-top: 15px;
            .btn{
            @include btn (#819ca9, 100%);
                &:nth-child(2){
                    @include btn(#d32f2f, 100%);
                    margin: 5px 0px;
                }
            }
        }
    }
}

@media screen and (max-width: 390px){
    .card{
        width: 150px;
        align-items: center;
        justify-content: center;
        .card-body__img-box{
            width: 100%;
            height: 120px;
            .card-body__img{
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
    }
}