.budge{
    height: 19px;
    width: max-content;
    min-width: 19px;
    border-radius: 50%;
    padding: 3px;
    background-color: rgba($color:#d32f2f, $alpha: 1);
    color: rgba($color: white, $alpha: 1);
    text-align: center;
    font-size: 14px;
    position: absolute;
    right: -9px;
    top: -7px;
}