.basket{
    background-color: rgba($color:#29434e, $alpha: .8);
    position: absolute;
    left: 32%;
    padding: 10px;
    width: 650px;
    max-height: 400px;
    overflow: auto;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
    align-content: center;
    border-radius: 4px;
    .full-price{
        padding: 10px;
        color: white;
        font-size: 20px;
    }
    .btn{
        text-align: center;
        cursor: pointer;
    }
}


@media screen and (max-width: 1080px){
    .basket{
        left: 20%;
    }
}

@media screen and (max-width: 900px){
    .basket{
        left: 10%;
    }
}

@media screen and (max-width: 800px){
    .basket{
        left: 3%;
        width: 90%;
    }
}

@media screen and (max-width: 500px){
    .basket{
        left: 2%;
        width: 90%;
    }
}