@import './button.scss';
@import './input.scss';


#header{
    background-color: #29434e;
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    color: rgba($color: white, $alpha: .8);
    .header-box{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        .image-box{
            width: min-content;
            .logo{
                width: 102px;
            }
        }
        .wrapper{
            display: flex;
            width: 70%;
            justify-content: space-between;
            .search-box{
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 600px;
                #searchInp{
                    @include inp(83%);
                }
                .btn{
                    @include btn;
                }
            }
            .cart-box{
                width: min-content;
                text-align: center;
                position: relative;
                .cart-icon{
                    object-fit: fill;
                    width: 42px;
                    cursor: pointer;
                }
            }
        }
    }
}

@media only screen and (max-width: 1020px){
    #header{
        height: auto;
        .header-box{
            padding-bottom: 5px;
            .image-box{
                width: 120px;
                .logo{
                    width: 100%;
                    object-fit: contain;
                }
            }
            .wrapper{
                width: 84%;
            }
        }
    }
}

@media only screen and (max-width: 790px){
     #header{
        .header-box{
            justify-content: center;
            padding-bottom: 5px;
            .wrapper{
                flex-wrap: wrap;
                justify-content: space-evenly;
                width: 100%;
                .cart-box{
                    margin-top: 5px;
                }
            }
        }
    }
}


@media only screen and (max-width: 460px){
     #header{
        .header-box{
            .wrapper{
                .search-box{
                    flex-wrap: wrap;
                    justify-content: space-around;
                    height: 80px;
                    align-items: center;
                    #searchInp{
                        @include inp(70%);
                    }
                }
            }
        }
    }
}


@media only screen and (max-width: 412px){
     #header{
        .header-box{
            .wrapper{
                .search-box{
                    height: 62px;
                    justify-content: space-around;
                }
            }
        }
    }
}