.filter-badge{
    border: none;
    border-radius: 0px 20px 20px 0px;
    font-size: 1.1rem;
    padding: 5px 10px;
    padding-left: 15px;
    cursor: pointer;
    margin: 5px;
    margin-left: 0px;
    height: 35px;
    line-height: 1.6;
    overflow: hidden;
    width: 55%;
    transition: all .3s ease-in;
    &:hover{
        width: 70%;
    }
}

@media screen and (max-width: 800px){
    .filter-badge{
        border-radius: 20px 20px 20px 20px;
        text-align: center;
        height: 30px;
        line-height: 1.3;    
    }
}
